@import "../utils/vars";
@import "../utils/mixins";
@import "../utils/extends";

.hours {
  &__overtitle {
    font-weight: 700;
    font-size: 12px;
    line-height: 140%;
    text-align: start;
    text-decoration: none;
 
    color: $redsite;
  }
 
  &__title {
    font-weight: 700;
    font-size: 32px;
    line-height: 32px;
    letter-spacing: -1.5px;
 
    text-align: start;
 
    margin: 2px 0 32px;
  }
 
  &__information {
    @extend %normal-text;
    text-align: start;
 
    &-title {
      font-weight: 700;
      font-size: 24px;
      line-height: 30px;
      margin-bottom: 16px
    }
 
    &-week {
      display: flex;
      gap: 25px;
      margin-bottom: 16px;
    }
 
    &-day {
      display: flex;
      flex-direction: column;
      row-gap: 8px;
      font-weight: 400;
 
      &--on-avenue {
        margin-bottom: 24px;
      }
    }
 
    &-hour {
      display: flex;
      flex-direction: column;
      row-gap: 8px
    }
  }
}
